import React, { useEffect, useRef } from 'react';
import styled, { css } from 'styled-components';
import { Search } from 'styled-icons/fa-solid/Search';
import config from '../../../config.js';

const SearchIcon = styled(Search)`
  width: 1em;
  pointer-events: none;
  margin-right: 10px;
`;
const focus = css`
  background: white;
  color: ${props => props.theme.darkBlue};
  cursor: text;
  width: 5em;
  + ${SearchIcon} {
    color: ${props => props.theme.darkBlue};
    margin: 0.3em;
  }
`;
const collapse = css`
  width: 0;
  cursor: pointer;
  color: ${props => props.theme.lightBlue};
  + ${SearchIcon} {
    color: white;
  }
  ${props => props.focus && focus}
  margin-left: ${props => (props.focus ? `-1.6em` : `-1em`)};
  padding-left: ${props => (props.focus ? `1.6em` : `1em`)};
  ::placeholder {
    color: ${props => props.theme.gray};
  }
  `;
const expand = css`
  background: ${props => props.theme.veryLightGray};
  width: 6em;
  margin-left: -1.6em;
  padding-left: 1.6em;
  + ${SearchIcon} {
    margin: 0.3em;
  }
`;
const Input = styled.input`
  outline: none;
  border: none;
  font-size: 1em;
  background: white;
  transition: ${props => props.theme.shortTrans};
  border-radius: ${props => props.theme.smallBorderRadius};
  {hightlight-next-line}
  ${props => (props.collapse ? collapse : expand)};
`;

const InputForm = props => {
  const searchCustom = '';
  const search = useRef(null);
  const inputEl = useRef(null);
  const className = `searchInput${searchCustom ? ' ' + searchCustom : ''}`;
  const inputSelector = `.searchInput${searchCustom ? '.' + searchCustom : ''}`;

  useEffect(() => {
    const { algoliaApiKey: apiKey, indexName, debug, autocompleteOptions } = config.header.search;
    if (!global.docsearch) return;

    search.current = global.docsearch({
      apiKey,
      indexName,
      inputSelector,
      debug,
      autocompleteOptions,
    });

    return () => search.current.autocomplete.autocomplete.destroy();
  }, [searchCustom]);

  const preventSubmit = event => {
    event.preventDefault();
  };

  const handleKeyDown = event => {
    const { altKey, keyCode, shiftKey } = event;

    if (keyCode === 27 && !(shiftKey || altKey)) {
      // ESC
      const autocomplete = search.current.autocomplete.autocomplete;
      const existingVal = autocomplete.getVal();
      if (existingVal) {
        autocomplete.setVal('');
      } else {
        inputEl.current.blur();
      }
      event.preventDefault();
      event.stopPropagation();
    }
  };

  return (
    <form className={'formElement'} onSubmit={preventSubmit}>
      <SearchIcon />
      <Input
        className={className}
        type="text"
        placeholder="Search"
        aria-label="Search"
        onKeyDown={handleKeyDown}
        ref={inputEl}
        {...props}
      />
    </form>
  );
};

export default InputForm;
